import React, {useState} from "react";
import tactJs from 'tact-js';
import Utils from '../utils'


export default () => {
  const [state, setState] = useState({
    dotPoints: [{index: 0, intensity: 100}],
    durationMillis: 1000,
    position: 'VestFront',
    key: 'dotMode',
  });

  const [tempPoint, setTempPoint] = useState({
    index: 0, intensity: 100,
  });

  const [errorMessage, setErrorMessage] = useState('');


  return (
    <div>
      <div>
        {
          !!errorMessage &&
          <div className={'border p-2 border-red-500 text-red-500'}>
            {errorMessage}
          </div>
        }
        <div className={'my-2'}>
          index:
          <input className={'w-12  mx-2 border'} onChange={(e) => {
            const value = Utils.clamps(e.target.value, 0, 19);

            if (isNaN(value)) {
              setErrorMessage('Invalid value (should be  [0~19])');
              return;
            }

            setTempPoint({
              ...tempPoint,
              index: value,
            })
            setErrorMessage('');
          }}
                 value={tempPoint.index}/>
                 intensity:
          <input className={'w-12  mx-2 border'} onChange={(e) => {
            const value = Utils.clamps(e.target.value, 0, 100);

            if (isNaN(value)) {
              setErrorMessage('Invalid value (should be  [0~100])');
              return;
            }

            setTempPoint({
              ...tempPoint,
              intensity: value,
            })
            setErrorMessage('');
          }}
                 value={tempPoint.intensity}/>
          <button className={'mx-2 bg-gray-500 p-2 w-8 rounded'}
                  onClick={() => {
                    let founded = state.dotPoints.find(d => d.index === tempPoint.index);
                    if (!!founded) {
                      setErrorMessage('Invalid value (already added with index ' + tempPoint.index);
                      return;
                    }


                    setState({
                      ...state,
                      dotPoints: [...state.dotPoints, tempPoint],
                    });
                    setErrorMessage('')
                  }}>+</button>
        </div>


        {
          state.dotPoints.map((point, index) => {
            return (
              <div key={index}>
                {
                  `index: ${point.index}, intensity: ${point.intensity}`
                }
                <button className={'border rounded p-2 mx-4 bg-gray-500'}
                        onClick={()=> {
                          setState({
                            ...state,
                            dotPoints: state.dotPoints.filter((p, pIndex) => pIndex !== index)
                          })
                        }}>-</button>
              </div>
            );
          })
        }

      </div>
      <div className={'my-8'}>

        <select className={'border p-2 mr-2'}
                onChange={(e) => {
                  setState({
                    ...state,
                    position: e.target.value,
                  })
                }}>
          <option>VestFront</option>
          <option>VestBack</option>
          <option>Head</option>
          <option>ForearmL</option>
          <option>ForearmR</option>
        </select>

        <input className={'border w-24 mr-2 p-2'}
               value={state.durationMillis}
               onChange={e => {
                const durationMillis = Utils.clamps(e.target.value, 0, 10000);
                setState({
                  ...state,
                  durationMillis,
                })
               }}>
        </input>
        ms

        <button className={'border rounded p-2 mx-4 bg-gray-500'}
                onClick={() => {
                  tactJs.submitDot(state.key, state.position, state.dotPoints, state.durationMillis);
                }}>
          Test
        </button>

        <button className={'border rounded p-2  bg-gray-500'}
                onClick={() => {
                  tactJs.turnOff(state.key);
                }}>Turn Off</button>
      </div>
    </div>
  );
}