import React, {useState} from "react";
import Utils from "../utils";
import tactJs from "tact-js";

export default () => {
  const [state, setState] = useState({
    pathPoints: [{x: 0.5, y: 0.5, intensity: 100}],
    durationMillis: 1000,
    position: 'VestFront',
    key: 'pathMode',
  });

  const [tempPoint, setTempPoint] = useState({
    x: 0.5, y: 0.5, intensity: 100,
  });

  const [errorMessage, setErrorMessage] = useState('');


  return (
    <div>
      Path Mode


      {
        !!errorMessage &&
        <div className={'border p-2 border-red-500 text-red-500'}>
          {errorMessage}
        </div>
      }
      <div className={'my-2'}>
        x:
        <input className={'w-12  mx-2 border'} onChange={(e) => {
          const value = Utils.clamps(e.target.value, 0, 1);

          if (isNaN(value)) {
            setErrorMessage('Invalid value (should be  [0~1])');
            return;
          }

          setTempPoint({
            ...tempPoint,
            x: value,
          })
          setErrorMessage('');
        }}
               value={tempPoint.x}/>
        y:
        <input className={'w-12  mx-2 border'} onChange={(e) => {
          const value = Utils.clamps(e.target.value, 0, 1);

          if (isNaN(value)) {
            setErrorMessage('Invalid value (should be  [0~1])');
            return;
          }

          setTempPoint({
            ...tempPoint,
            y: value,
          })
          setErrorMessage('');
        }}
               value={tempPoint.y}/>
         intensity:
        <input className={'w-12  mx-2 border'} onChange={(e) => {
          const value = Utils.clamps(e.target.value, 0, 100);

          if (isNaN(value)) {
            setErrorMessage('Invalid value (should be  [0~100])');
            return;
          }

          setTempPoint({
            ...tempPoint,
            intensity: value,
          })
          setErrorMessage('');
        }}
               value={tempPoint.intensity}/>
        <button className={'mx-2 bg-gray-500 p-2 w-8 rounded'}
                onClick={() => {

                  setState({
                    ...state,
                    pathPoints: [...state.pathPoints, tempPoint],
                  });
                  setErrorMessage('')
                }}>+</button>
      </div>


      {
        state.pathPoints.map((point, index) => {
          return (
            <div key={index}>
              {
                `x: ${point.x}, y: ${point.y},  intensity: ${point.intensity}`
              }
              <button className={'border rounded p-2 mx-4 bg-gray-500'}
                      onClick={()=> {
                        setState({
                          ...state,
                          pathPoints: state.pathPoints.filter((p, pIndex) => pIndex !== index)
                        })
                      }}>-</button>
            </div>
          );
        })
      }



      <div className={'my-8'}>

        <select className={'border p-2 mr-2'}
                onChange={(e) => {
                  setState({
                    ...state,
                    position: e.target.value,
                  })
                }}>
          <option>VestFront</option>
          <option>VestBack</option>
          <option>Head</option>
          <option>ForearmL</option>
          <option>ForearmR</option>
        </select>

        <input className={'border w-24 mr-2 p-2'}
               value={state.durationMillis}
               onChange={e => {
                 const durationMillis = Utils.clamps(e.target.value, 0, 10000);
                 setState({
                   ...state,
                   durationMillis,
                 })
               }}>
        </input>
        ms

        <button className={'border rounded p-2 mx-4 bg-gray-500'}
                onClick={() => {
                  tactJs.submitPath(state.key, state.position, state.pathPoints, state.durationMillis);
                }}>
          Test
        </button>

        <button className={'border rounded p-2  bg-gray-500'}
                onClick={() => {
                  tactJs.turnOff(state.key);
                }}>Turn Off</button>
      </div>
    </div>
  );
}