import React, {useEffect, useState} from "react";
import tactJs from 'tact-js';
import Block_L from '../tactFiles/ArmLeft/Block_L';
import Electric_L from '../tactFiles/ArmLeft/Electric_L';
import Pistol_L from '../tactFiles/ArmLeft/Pistol_L';

import Block_R from '../tactFiles/ArmRight/Block_R';
import Electric_R from '../tactFiles/ArmRight/Electric_R';
import Pistol_R from '../tactFiles/ArmRight/Pistol_R';

import Explosion from '../tactFiles/Vest/Explosion';
import Melee1 from '../tactFiles/Vest/Melee1';
import Melee2 from '../tactFiles/Vest/Melee2';
import Heartbeat from '../tactFiles/Vest/Heartbeat';
import PistolHit from '../tactFiles/Vest/PistolHit';

import ExplosionHead from '../tactFiles/Head/ExplosionHead';
import HeatHead from '../tactFiles/Head/HeatHead';

const tactFiles = [
  Block_L,
  Electric_L,
  Pistol_L,

  Block_R,
  Electric_R,
  Pistol_R,

  Explosion,
  Melee1,
  Melee2,
  Heartbeat,
  PistolHit,

  ExplosionHead,
  HeatHead,
];

export default () => {
  const [tactFile, setSetFile] = useState(0);

  useEffect(() => {
    tactJs.turnOffAll();
    setTimeout(() => {

      tactJs.registerFile(tactFile+'', JSON.stringify(tactFiles[tactFile]))
    }, 100)
  }, []);

  return (
    <div>
      <div>
        Tact Files
      </div>

      <select className={'border p-2'}
              value={tactFile}
              onChange={(e) => {
                tactJs.registerFile(e.target.value, JSON.stringify(tactFiles[e.target.value]))
                setSetFile(e.target.value);
              }}>
        {tactFiles.map((file, index) => {
          return (
            <option key={index} value={index}>
              {
                file.project.layout.name + ' - '
              }

              {
                file.project.name
              }
            </option>
          );
        })}
      </select>


      <div className={'my-2'}>
        <button onClick={() => {
          tactJs.submitRegistered(tactFile)
        }}
                className={'border p-2 bg-gray-500 rounded w-20'}>Play</button>
      </div>
    </div>
  );
}