import React, {useEffect} from "react";
import tactJs from "tact-js";

export default ({onChange}) => {
  useEffect(() => {
    tactJs.addListener((msg) => {
      if (!msg.message.Status) {
        return;
      }

      const status = msg.message.Status;

      const back = status.VestBack;
      const front = status.VestFront;
      onChange(front, back,  status.ForearmL, status.ForearmR, status.Head);
    });

    setInterval(() => {
      // this is for ping()
      tactJs.turnOff('');
    }, 4000);
  }, []);

  return (
    <div style={{display: "none"}}></div>
  );
}