import React, {useEffect, useState} from "react";
import tactJs from "tact-js";

export default () => {
  const [status, setStatus] = useState("");

  useEffect(() => {
    tactJs.addListener((msg) => {
      if (status !== msg.status) {
        setStatus(msg.status);
      }
    })
  }, []);

  return (
    <div className={'my-2'}>
      <a href={'https://bhaptics.com/download'}>
        {`bHaptics Player : `}
      </a>
      {
        status === 'Connected' ?
        <span className={'bg-green-500 p-2 rounded'}>
          {status}
        </span> :
        <React.Fragment>
          <span className={'bg-red-500 p-2 rounded'}>
            {status}
          </span>
        </React.Fragment>
      }


    </div>
  );
}